import './Presentation.css';
import { Link } from 'react-router-dom';
import presentationImage from './assets/presentation-image.png';

function Presentation({ isAuthorized }) {
    let elementRequestData = (
        <Link to='/search'>
            <button className='presentation-request-data'> </button>
        </Link>
    );

    return (
        <section className={isAuthorized ? 'presentation' : 'presentation presentation_without-button'}>
            <h1 className='presentation-title'>  <br /><br /> <br />  </h1>
            <p className='presentation-description'>  ,     PDF   .</p>
            {isAuthorized && elementRequestData}
            <img className={isAuthorized ? 'presentation-image' : 'presentation-image presentation-image_without-button'} src={presentationImage} alt=' ,   ' />
        </section>
    );
}

export default Presentation;